import { FindFirstFunction } from "../GadgetFunctions.js";
import { GadgetRecord } from "../GadgetRecord.js";
import { RuntimeAdapter } from "../adaptors/types.js";
import { CoreHooks } from "../createHooks.js";
import { findManyOperation } from "../operationBuilders.js";
import { get, hydrateConnection, namespaceDataPath } from "../support.js";
import { DefaultSelection, LimitToKnownKeys, Select } from "../types.js";
import { ErrorWrapper, OptionsType, ReadHookResult, ReadOperationOptions, useQueryArgs } from "./utils.js";

export const createUseMaybeFindFirst = (adapter: RuntimeAdapter, coreHooks: CoreHooks) => {
  const useMaybeFindFirst = <
    GivenOptions extends OptionsType,
    SchemaT,
    F extends FindFirstFunction<GivenOptions, any, SchemaT, any>,
    Options extends F["optionsType"] & ReadOperationOptions
  >(
    manager: { findFirst: F },
    options?: LimitToKnownKeys<Options, F["optionsType"] & ReadOperationOptions>
  ): ReadHookResult<null | GadgetRecord<
    Select<Exclude<F["schemaType"], null | undefined>, DefaultSelection<F["selectionType"], Options, F["defaultSelection"]>>
  >> => {
    const firstOptions = { ...options, first: 1 };
    const memoizedOptions = coreHooks.useStructuralMemo(firstOptions);
    const plan = adapter.framework.useMemo(() => {
      return findManyOperation(
        manager.findFirst.operationName,
        manager.findFirst.defaultSelection,
        manager.findFirst.modelApiIdentifier,
        memoizedOptions,
        manager.findFirst.namespace
      );
    }, [manager, memoizedOptions]);

    const [rawResult, refresh] = coreHooks.useGadgetQuery(useQueryArgs(plan, firstOptions));

    const result = adapter.framework.useMemo(() => {
      const dataPath = namespaceDataPath([manager.findFirst.operationName], manager.findFirst.namespace);
      let data = rawResult.data ?? null;
      if (data) {
        const connection = get(rawResult.data, dataPath);
        if (connection) {
          data = hydrateConnection(rawResult, connection)[0] ?? null;
        } else {
          data = data[0] ?? null;
        }
      }

      const error = ErrorWrapper.forMaybeCombinedError(rawResult.error);

      return {
        ...rawResult,
        error,
        data,
      };
    }, [rawResult, manager]);

    return [result, refresh];
  };

  return {
    /**
     *  Hook to fetch many Gadget records using the `maybeFindFirst` method of a given manager.
     *
     * @param manager Gadget model manager to use
     * @param options options for filtering and searching records, and selecting the fields in each record of the result
     *
     * @example
     *
     * ```
     * export function Users() {
     *   const [result, refresh] = useMaybeFindFirst(Client.user, {
     *     select: {
     *       name: true,
     *     },
     *   });
     *
     *   if (result.error) return <>Error: {result.error.toString()}</>;
     *   if (result.fetching && !result.data) return <>Fetching...</>;
     *   if (!result.data) return <>No user found</>;
     *
     *   return <div>{result.data.name}</div>;
     * }
     * ```
     */
    useMaybeFindFirst,
  };
};
